/* TopTracks.css */
.top-tracks-page {
  font-family: "Circular std";
  width: calc(100% - 130px);
  /* margin-left: 70px; */
  margin-top: 70px;
  padding-bottom: 70px;
  display: flex;
  justify-content: center;
}
/* Container styles */
.t-container {
  display: flex;
  flex-direction: column;
  width: 1130px;
  gap: 25px;
  align-items: flex-start;
  margin-left: 130px;
}

.top-track-select-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
}
/* Header styles */
.tracks-h2 {
  justify-self: flex-start;
  font-size: 2rem;
  color: #1db954;
  text-align: center;
  margin: 0;
}

/* Select dropdown styles */
.top-track-select {
  background-color: #282828;
  color: #ffffff;
  height: 40px;
  border: 1px solid #3e3e3e;
  padding: 0.5rem;
  border-radius: 10px;
  justify-self: flex-end;
}

.top-track-page-container {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.page-song-list {
  display: flex;
  flex-direction: column;
  gap: 25px; /* Space between each song item */
}

.top-song-item {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}

.top-song-image {
  width: 50px;
  height: 50px;
  margin-right: 15px;
}

.top-song-info {
  flex-grow: 1;
}

.top-song-name {
  font-size: 16px;
  font-weight: 400;
  color: #ffffff;
  margin-bottom: 5px;
}

.top-artist-album {
  font-size: 14px;
  color: #9b9b9b;
}

.top-artist {
  margin-right: 5px;
}
.top-album {
  margin-left: 5px;
}

.top-song-duration {
  font-size: 14px;
  color: #9b9b9b;
  justify-self: flex-end;
}
