nav {
  position: fixed;
  top: 0;
  left: 0;
  background-color: #1f1d1d;
  width: 130px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-top: 20px;
  overflow: hidden;
}

.spotify-logo {
  margin-bottom: 30px;
}

.links {
  position: relative;
  bottom: 50px;
  width: 100%;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
}

.navbar-ul {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
}

.navbar-ul li {
  display: flex;
  height: 6.5rem;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0px;
  color: white;
  cursor: pointer;
  transition: color 0.3s ease, background-color 0.3s ease;
}

.navbar-ul li .icon {
  font-size: 25px; /* Make the icon size larger for the profile */
}
.icon {
  font-size: 20px;
}

.navbar-ul li .text {
  font-size: 14px;
}

.link-a {
  text-decoration: none;
  color: white;
  display: flex;
  gap: 10px;
  padding: 10px;
  flex-direction: column;
  align-items: center;
}

.navbar-ul li:hover {
  background-color: #040306;
  color: #ffffff;
}

.navbar-ul li.active {
  background-color: #040306;
  color: white;
}
