.logout-button {
  position: relative;
  margin: 30px;
  font-family: "Circular std";
  width: 117px;
  height: 40px;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #ffffff;
  border: solid;
  border-radius: 25px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2); /* Subtle shadow for the button */
  font-weight: 500;
  font-style: normal;
}
.home-page {
  position: relative;
  padding: 20px;
  overflow: hidden;
  width: 100%;
  justify-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Circular std";
}

.profile-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 500px;
  margin: 20px;
  border-bottom: 1px solid #9b9b9b;
}

.profile-image {
  position: relative;
  border-radius: 50%;
  width: 130px;
  height: 130px;
  top: 0;
  align-self: center;
  border: 4px solid #1db954;
  justify-items: center;
}

.username {
  font-size: 3rem;
  margin: 20px;
  color: #ffffff;
}

.stats {
  display: flex;
  flex-direction: row;
  gap: 50px;
}

.followers-container {
  font-size: 24px;
  text-align: center;
}
.followers {
  margin: 5px;
  margin-top: 0;
  color: #1db954;
}

.playlists {
  margin: 5px;
  margin-top: 0;
  color: #1db954;
}
.playlists-container {
  font-size: 24px;
  text-align: center;
}

.followers-text,
.playlists-text {
  margin: 0;
  font-size: 16px;
  color: #9b9b9b;
}

.top-container {
  display: flex;
  flex-direction: row;
  gap: 50px;
  width: 100%;
}

.top-track-container {
  flex: 1;
  position: relative;
  display: flex;
  flex-direction: column;
}

.top-shelf {
  position: relative;
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.top-artist-container {
  flex: 1;
  position: relative;
  display: flex;
  flex-direction: column;
}

.top-track-h2,
.top-artist-h2 {
  position: relative;
  font-size: 24px;
  color: #ffffff;
}

.see-more-button {
  position: relative;
  right: 0;
  margin: 15px 0;
  font-family: "Circular std";
  width: 117px;
  height: 40px;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #ffffff;
  border: solid;
  border-radius: 25px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2); /* Subtle shadow for the button */
  font-weight: 500;
  font-style: normal;
}

.song-list {
  display: flex;
  flex-direction: column;
  gap: 15px; /* Space between each song item */
}

.song-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  border-bottom: 1px solid #9b9b9b; /* Line between songs */
}

.song-image {
  width: 50px;
  height: 50px;
  margin-right: 15px;
}

.song-info {
  flex-grow: 1;
}

.song-name {
  font-size: 16px;
  font-weight: 400;
  color: #ffffff;
  margin-bottom: 1px;
}

.artist-album {
  font-size: 14px;
  color: #9b9b9b;
}

.artist {
  margin-right: 5px;
}
.album {
  margin-left: 5px;
}

.song-duration {
  font-size: 14px;
  color: #9b9b9b;
  margin-left: 20px;
}

.top-artist-list {
  display: flex;
  flex-direction: column;
  gap: 15px; /* Space between each artist item */
}

.top-artist-item {
  display: flex;
  align-items: center;
  justify-content: space-between; /* Ensure popularity stays on the right */
  padding: 10px;
  border-bottom: 1px solid #9b9b9b;
}

.top-artist-image {
  width: 50px;
  height: 50px;
  border-radius: 50%; /* Make the image circular */
  margin-right: 15px;
}

.top-artist-info {
  flex-grow: 1; /* Allow artist name to grow */
}

.top-artist-name {
  font-size: 16px;
  font-weight: 400;
  color: #ffffff;
}

.top-artist-popularity {
  font-size: 14px;
  color: #9b9b9b;
  margin-left: 20px; /* Add space between name and popularity */
}
