.albums-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  overflow: hidden; /* Ensures the background animation doesn't affect layout */
}

/* Glass Card with Darker Background and Stronger Shadow */
.glass-card {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  max-width: 500px;
  padding: 2rem;
  background: rgba(0, 0, 0, 0.4); /* Darkened background */
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.37); /* Stronger shadow */
  backdrop-filter: blur(15px); /* Increased blur for stronger glass effect */
  -webkit-backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  z-index: 2;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.glass-card h3 {
  font-size: 2.5rem;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-style: normal;
  color: white;
  margin: 0;
  text-shadow: 0 2px 5px rgba(0, 0, 0, 0.5); /* Add depth to the text */
}

/* Button style for better aesthetics */
.login-button {
  background-color: #1db954;
  color: white;
  width: 50%;
  border: none;
  margin: 8px;
  padding: 0.9rem 1.5rem;
  border-radius: 25px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2); /* Subtle shadow for the button */
  align-self: center;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-style: normal;
}

.login-button:hover {
  background-color: #1aa34a;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3); /* More prominent shadow on hover */
}

.image {
  z-index: 1;
  width: 225px;
  height: 225px;
}

.wrapper {
  width: 100%;
  max-width: 1536px;
  margin-inline: auto;
  position: relative;
  height: 245px;
  overflow: hidden;
  mask-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 1) 20%,
    rgba(0, 0, 0, 1) 80%,
    rgba(0, 0, 0, 0)
  );
  z-index: 1;
}

/* Animation for scrolling images */
@keyframes scrollLeft {
  to {
    left: -200px;
  }
}

@keyframes scrollRight {
  to {
    right: -200px;
  }
}

.item {
  width: 225px;
  height: 225px;
  border-radius: 6px;
  position: absolute;
  animation-duration: 30s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.right-to-left {
  left: max(calc(225px * 8), 100%);
  animation-name: scrollLeft;
}

.left-to-right {
  right: max(calc(225px * 8), 100%);
  animation-name: scrollRight;
}

/* Staggered animation delays */
.item1 {
  animation-delay: calc(30s / 8 * (8 - 1) * -1);
}

.item2 {
  animation-delay: calc(30s / 8 * (8 - 2) * -1);
}

.item3 {
  animation-delay: calc(30s / 8 * (8 - 3) * -1);
}

.item4 {
  animation-delay: calc(30s / 8 * (8 - 4) * -1);
}

.item5 {
  animation-delay: calc(30s / 8 * (8 - 5) * -1);
}

.item6 {
  animation-delay: calc(30s / 8 * (8 - 6) * -1);
}

.item7 {
  animation-delay: calc(30s / 8 * (8 - 7) * -1);
}

.item8 {
  animation-delay: calc(30s / 8 * (8 - 8) * -1);
}
