/* TopArtists.css */
.top-artist-page {
  display: flex;
  justify-content: center;
  font-family: "Circular std";
  margin-top: 70px;
  padding-bottom: 70px;
}
/* Container styles */
.container {
  display: flex;
  flex-direction: column;
  width: 1300px;
  gap: 25px;
  margin-left: 130px;
}
.top-artist-select-container {
  display: flex;
  margin: 0 70px;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
/* Header styles */
.artist-h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #1db954;
  text-align: center;
  justify-self: flex-start;
}

/* Select dropdown styles */
.top-artist-select {
  background-color: #282828;
  color: #ffffff;
  height: 40px;
  border: 1px solid #3e3e3e;
  padding: 0.5rem;
  border-radius: 10px;
  justify-self: flex-end;
}

/* Artist list styles */
.artist-list {
  list-style: none;
  width: 100%;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
}

.artist-item {
  background-color: transparent;
  padding: 1rem;
  border-radius: 10px;
  text-align: center;
  transition: background-color 0.3s, transform 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.artist-item:hover {
  background-color: #282828;
  transform: translateY(-5px);
}

/* Artist image styles */
.artist-image {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  margin-bottom: 1.5rem;
}

/* Artist info styles */
.artist-info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.artist-name {
  font-size: 1.2rem;
  color: #ffffff;
  margin: 0;
}
